// components/ProductDetail.js

import React from 'react';
import { Breadcrumbs, Typography, Button, Radio, FormControlLabel, RadioGroup } from '@mui/material';
import Link from 'next/link';
import API_BASE_URL from "@/utils/API"
const fetchProduct = async (id) => {
    
}
const ProductDetail = ({ product }) => {
  return (
    <section className="py-12 sm:py-16">
      <div className="container mx-auto px-4">
        {/* Breadcrumb */}
        <nav>
          <Breadcrumbs separator="/" aria-label="breadcrumb">
            <Link href="/" className="text-gray-600 hover:text-gray-800">
              Home
            </Link>
            <Link href="/products" className="text-gray-600 hover:text-gray-800">
              Products
            </Link>
            <Typography color="textPrimary">Coffee</Typography>
          </Breadcrumbs>
        </nav>

        <div className="grid grid-cols-1 gap-12 lg:mt-12 lg:grid-cols-5 lg:gap-16 mt-8">
          {/* Product Image Section */}
          <div className="lg:col-span-3">
            <div className="lg:flex lg:items-start">
              <div className="lg:order-2 lg:ml-5">
                <div className="max-w-xl overflow-hidden rounded-lg">
                  <img className="h-full w-full object-cover" src="/images/JHxMnVrtPMdcNU1s_7g7f.png" alt="Coffee" />
                </div>
              </div>

              <div className="mt-2 w-full lg:order-1 lg:w-32 lg:flex-shrink-0">
                <div className="flex flex-row lg:flex-col">
                  <img className="mb-3 h-20 rounded-lg border-2 border-gray-900 object-cover" src="/images/JHxMnVrtPMdcNU1s_7g7f.png" alt="Coffee Thumbnail" />
                  <img className="mb-3 h-20 rounded-lg border-2 border-transparent object-cover" src="/images/JHxMnVrtPMdcNU1s_7g7f.png" alt="Coffee Thumbnail" />
                  <img className="mb-3 h-20 rounded-lg border-2 border-transparent object-cover" src="/images/JHxMnVrtPMdcNU1s_7g7f.png" alt="Coffee Thumbnail" />
                </div>
              </div>
            </div>
          </div>

          {/* Product Details Section */}
          <div className="lg:col-span-2">
            <Typography variant="h4" component="h1" className="font-bold">
              Afro-Brazillian Coffee
            </Typography>

            <div className="mt-5 flex items-center">
              <div className="flex items-center text-yellow-500">
                {Array(5)
                  .fill('')
                  .map((_, i) => (
                    <svg key={i} className="h-4 w-4 fill-current" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20">
                      <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z"></path>
                    </svg>
                  ))}
              </div>
              <Typography variant="body2" className="ml-2 text-gray-500">
                1,209 Reviews
              </Typography>
            </div>

            <div className="mt-8">
              <Typography variant="body1" className="text-gray-900">
                Coffee Type
              </Typography>
              <RadioGroup row>
                {['Powder', 'Whole Bean', 'Ground'].map((type) => (
                  <FormControlLabel
                    key={type}
                    value={type}
                    control={<Radio />}
                    label={
                      <Typography className="peer-checked:bg-black peer-checked:text-white border border-black px-6 py-2 font-bold rounded-lg">
                        {type}
                      </Typography>
                    }
                  />
                ))}
              </RadioGroup>
            </div>

            <div className="mt-8">
              <Typography variant="body1" className="text-gray-900">
                Choose subscription
              </Typography>
              <RadioGroup row>
                {['4 Months', '8 Months', '12 Months'].map((plan, index) => (
                  <FormControlLabel
                    key={index}
                    value={plan}
                    control={<Radio />}
                    label={
                      <div>
                        <Typography className="peer-checked:bg-black peer-checked:text-white border border-black px-6 py-2 font-bold rounded-lg">
                          {plan}
                        </Typography>
                        <Typography variant="caption" display="block">
                          {index === 0 ? '$80/mo' : index === 1 ? '$60/mo' : '$40/mo'}
                        </Typography>
                      </div>
                    }
                  />
                ))}
              </RadioGroup>
            </div>

            <div className="mt-10 flex items-center justify-between space-y-4 border-t border-b py-4">
              <div className="flex items-end">
                <Typography variant="h5" className="font-bold">
                  $60.50
                </Typography>
                <Typography variant="body2">/month</Typography>
              </div>
              <Button variant="contained" color="primary" className="bg-gray-900 text-white hover:bg-gray-800 px-12 py-3">
                Add to Cart
              </Button>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default ProductDetail;
